    private initData = {
      'H_{up}': 233,
      'H_{down}': 56,
      K: 1.4,
      Q: 14600,
      b: 50,
      't^{\\prime}': 19
    }
  private data = {
    'H_{up}': '',
    'H_{down}': '',
    K: '',
    Q: '',
    b: '',
    't^{\\prime}': ''
  }

  private formulas = {
    0: '$$K :综合冲刷系数$$',
    1: '$$Q :流量,m^3/s$$',
    2: '$$b :鼻坎宽度,m$$',
    3: '$$t^{\\prime} :下游水深,m$$',
    4: '$$H_{up} :上游水面高程，m$$',
    5: '$$H_{down} :下游水面高程，m$$',
    6: '$$H :上下游水面差，m$$'
  }

